.TimelineOptions{
    display:block;
    position:relative;
    margin:0;
    padding:10px 15px;
    width:100%;
    background-color:#000000;
}
.TimelineOptionsInner{
    display:block;
    position:relative;
    margin:0 auto;
    padding:0;
    width:auto;
}
.TimelineOptionsInner > ul{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    -ms-flex-pack:center;
        justify-content:center;
    position:relative;
    margin:0;
    padding:0;
    width:auto;
    text-align:center;
    list-style:none;
}
.TimelineOptionsInner > ul > li{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align:center;
        align-items:center;
    position:relative;
    margin:10px;
    padding:5px 10px;
    background-color:#FFFFFF;
    text-align:center;
}
.TimelineOptionsInner > ul *::-moz-selection{
    background-color:transparent;
    opacity:0;
}
.TimelineOptionsInner > ul *::selection{
    background-color:transparent;
    opacity:0;
}
#layerBattery-parent.active{
    background-color:rgb(25, 45, 230);
}
#layerUsage-parent.active{
    background-color:rgb(146, 54, 207);
}
#layerSolar-parent.active{
    background-color:rgb(230, 185, 39);
}
.valueRange{
    margin-left:10px;
}


