body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color:#000000;
}
* {
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
}
.bg-battery{
  background-color:rgb(25, 45, 230);
}
.bg-usage{
  background-color:rgb(2, 46, 2);
}
.border-usage{
  border-top:2px solid rgb(146, 54, 207);
}
.bg-solar{
  background-color:rgb(230, 185, 39);
}
.bg-surplus{
  background-color:rgb(42, 145, 42);
}
.bg-deficit{
  background-color:rgb(185, 110, 11);
}


