#TimelineView{
    display:block;
    position:relative;
    margin:0;
    padding:0;
    width:100%;
    background-color:#000000;
}
#TimelineBaseline{
    display:block;
    position:relative;
    margin:0;
    width:100%;
    height:1px;
    background-color:#999999;
}
#TimelineStatusContainer{
    display:block;
    position:relative;
    text-align:right;
    margin:10px 10px 0 0;
    color:#FFFFFF;
    font-size:10px;
}
#TimelineData{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack:start;
        justify-content:flex-start;
    -ms-flex-direction:row-reverse;
        flex-direction:row-reverse;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    position:relative;
    margin:0;
    width:100%;
    height:102px;
    max-height:102px;
    overflow:hidden;
    background-image:-webkit-gradient(linear,left top, left bottom,from(#333333),to(#111111));
    background-image:-webkit-linear-gradient(#333333,#111111);
    background-image:-o-linear-gradient(#333333,#111111);
    background-image:linear-gradient(#333333,#111111);
}
.dataContainer{
    position:relative;
    width:3px;
    height:100%;
}
.dataPoint{
    position:absolute;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    left:0;
    width:100%;
    height:2px;
    opacity:1;
}
.dataPoint.off{
    opacity:0;
}


